body {
  font-family: var(--font-opensans);
}

.container {
  width: 1352px;
  margin: 0 auto;
}
@media (max-width: 1352px) {
  .container {
    width: 100%;
    padding: 0 15px;
  }
}

.benefits {
  padding: 100px 0;
  background-color: var(--color-light-grey);
}

.benefits-container {
  padding: 0 100px;
}

.benefits-row {
  width: 1124px;
  height: 370px;
  background-color: var(--color-white);
  border: 1px solid #E8E9ED;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 37px;
  border-radius: 5px;
}

.benefits-row--notebook {
  --accent-color: var(--color-redish);
}

.benefits__textarea {
  max-width: 416px;
  margin: 90px 36px;
}

.benefits__textarea::after {
  content: "";
  width: 332px;
  height: 4px;
  background-color: var(--accent-color);
  display: block;
  margin-top: 50px;
}

.benefits__title {
  text-align: left;
  font-family: var(--font-raleway);
  font-size: 2rem;
  color: var(--color-black);
  margin-bottom: 30px;
}

.benefits__description {
  margin-top: 30px;
  line-height: 1.375rem;
}

.benefits__image {
  position: relative;
}

.benefits__image img {
  position: relative;
  z-index: 2;
}

.benefits-row--notebook .benefits__image img {
  width: 386px;
  height: auto;
}

.benefits-row--notebook .benefits__image {
  margin-right: 117px;
  margin-bottom: 38px;
}

.benefits-row--notebook .benefits__image::before {
  content: "";
  width: 180px;
  height: 180px;
  background-color: var(--accent-color);
  display: block;
  border-radius: 40px;
  position: absolute;
  right: 0;
  top: -40px;
  transform: rotate(45deg);
}

.benefits-row--phone {
  --accent-color: var(--color-primary);
  flex-direction: row-reverse;
  padding: 0 20px 0 70px;
}

.benefits-row--phone .benefits__image::before {
  content: "";
  width: 320px;
  height: 320px;
  background-color: var(--accent-color);
  display: block;
  border-radius: 50%;
  position: absolute;
  left: 0;
  top: -20px;
}

.benefits-row--phone .benefits__image img {
  width: 426px;
  height: auto;
}

.benefits-row--phone .benefits__textarea {
  margin: 70px 0;
}

.benefits-double {
  display: flex;
  --accent-color: var(--color-green);
}

.benefits__double > div {
  margin-bottom: 0;
}

.benefits-row--solo {
  max-width: 620px;
}

.benefits-row--solo .benefits__textarea {
  max-width: none;
  margin: 70px 35px;
}

.benefits-row--image {
  max-width: 440px;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--accent-color);
  border: none;
  margin-left: 60px;
}

.benefits-row--image img {
  width: 295px;
  height: auto;
  margin-top: 55px;
}

.contact {
  padding: 80px 100px 100px;
  display: flex;
  justify-content: center;
  margin-left: 365px;
  margin-right: 365px;
}

.contact-column1 {
  height: 376px;
  width: 50%;
}

.contact-title {
  text-align: left;
  font-size: 3.0625rem;
  line-height: 3.6875rem;
  font-family: var(--font-raleway);
  color: var(--color-black);
  margin-bottom: 22px;
}

.contact-description {
  text-align: left;
  font-size: 1rem;
  color: var(--color-black);
  line-height: 1.375rem;
  margin: 22px 0 42px;
}

.contact-details {
  display: inline-block;
  margin-top: 42px;
}

.contact-point {
  margin-right: 20px;
}

.contact-point img {
  margin-right: 10px;
}

.form {
  display: flex;
  flex-direction: column;
}

.contact-column2 {
  height: 376px;
  width: 50%;
}

.form__place {
  display: flex;
  flex-direction: column;
}

.form__place:first-of-type {
  margin-bottom: 42px;
}

.form__label {
  font-weight: bold;
  text-transform: uppercase;
  color: var(--color-primary);
  line-height: 0.9rem;
  letter-spacing: 3px;
  font-size: 0.6875rem;
}

.form__input {
  border: none;
  border-bottom: 1px solid var(--color-grey);
  background-color: transparent;
  line-height: 2.625rem;
}

.form__consent {
  margin-top: 30px;
}

.form__checkbox {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 1.375rem;
}

.form__checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.form__checkbox .checkbox {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: var(--color-white);
  border: 1px solid var(--color-grey);
  border-radius: 4px;
}

.form__checkbox input:checked ~ .checkbox {
  background-image: url(../assets/Checkmark.svg);
  background-position: center;
  background-size: 50%;
  background-repeat: no-repeat;
}

.form__checkbox .consent__text {
  margin: 0 10px;
  font-size: 0.75rem;
  color: var(--color-grey);
  line-height: 17px;
}

.form .btn {
  font-size: 0.8125rem;
  padding: 20px 97px;
  align-self: flex-end;
  margin-top: 20px;
  border: none;
  background-color: var(--color-primary);
  color: var(--color-white);
}

.footer {
  height: 89px;
  box-shadow: 0 -5px 40px #00000029;
  padding: 20px 0;
}

.footer-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__information {
  display: flex;
  flex-direction: column;
}

.footer__information .logo {
  text-align: left;
  font-size: 1.625rem;
  color: var(--color-primary);
  margin-bottom: 6px;
}

.footer__copy {
  font-size: 0.6875rem;
  color: var(--color-grey);
  margin-bottom: 17px;
}

.footer__media a:first-of-type {
  margin-right: 25px;
}

header {
  width: 100vw;
  height: 70px;
