}

.bar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.bar-logo__name {
  /*margin: 16px 17px auto 136px;*/
  text-align: left;
  font: normal normal 300 2rem var(--font-raleway);
  color: var(--color-primary);
