.opening-section {
  width: 100vw;
  height: 712px;
  margin-top: 130px;
  background-color: var(--color-white);
  text-align: center;
}

.opening-section-title {
  font-family: var(--font-raleway);
  font-size: 3.0625rem;
  letter-spacing: 0;
  color: var(--color-black);
