  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  text-align: center;
  font-family: var(--font-raleway);
}

.statistics__circle {
  border-radius: 50%;
  width: 63px;
  height: 63px;
  position: relative;
}

.statistics__img {
  position: absolute;
}

.statistics-column:nth-child(1) .statistics__circle {
  background-color: var(--color-redish);
}

.statistics-column:nth-child(1) .statistics__img {
  top: 0;
  left: 45%;
}

.statistics-column:nth-child(2) .statistics__circle {
  background-color: var(--color-primary);
}

.statistics-column:nth-child(2) .statistics__img {
  top: 14px;
  left: 47%;
}

.statistics-column:nth-child(3) .statistics__circle {
  background-color: var(--color-green);
}

.statistics-column:nth-child(3) .statistics__img {
  top: 6px;
  left: 50%;
}

.statistics__number {
  font-size: 1.25rem;
  font-weight: bold;
  letter-spacing: 0.3725rem;
  color: var(--color-black);
  margin-top: 14px;
}

.statistics__description {
  color: var(--color-grey);
  font-size: 1.0625rem;
  margin-top: 19px;
}